@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
body {
  margin: 0;
  font-family: Oswald, sans-serif;
}
img {
  max-width: 100%;
  object-fit: contain;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.button {
  font-style: Oswald, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  padding: 8px 44px;
  background: #68b738;
  display: block;
  text-align: center;
  border: none;
  text-transform: uppercase;
}
.button:hover {
  background: #5EAA2F;
}
.button:active {
  background: #5EAA2F;
  color: #ffffff75;
}
.button--secondary {
  font-style: Oswald, sans-serif;
  color: #444444;
  background: #F2F2F2;
  text-transform: uppercase;
}
.button--secondary:hover {
  background: #EBEBEB;
}
.button--secondary:active {
  background: #EBEBEB;
  color: #444444af;
}
.container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  padding: 0 30px;
}
@media (min-width: 1440px) {
  padding: 0 110px;
}
.main-header {
  background-color: #ffffff;
  padding: 10px 20px;
  min-width: 66px;
}
.main-header__info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 56px;
  justify-content: space-between;
}
.main-header__logo {
  margin: 0;
  height: 38px;
}
@media (max-width: 767px) {
  .main-header__toggle--nojs {
    display: none;
  }
  .main-header__toggle {
    margin: 0;
    padding: 0;
    z-index: 2;
    display: block;
    width: 24px;
    min-height: 16px;
    cursor: pointer;
    border: none;
    position: relative;
  }
  .main-header__toogle--opened {
    display: block;
  }
  .main-header__toogle--opened::before {
    width: 24px;
    height: 16px;
    position: absolute;
    content: "";
    background-image: url("../img/nav-opened.svg");
    top: 0;
    right: 15px;
  }
  .logo-img {
    height: 38px;
  }
  .logo-slogan {
    width: 101px;
    height: 18px;
  }
}
@media (min-width: 768px) {
  .main-header {
    padding: 0 30px;
    padding-top: 42px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    flex-grow: 1;
    margin: 0;
  }
  .main-header__info {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    gap: 12px;
  }
  .logo-img {
    width: 60px;
    height: 50px;
  }
  .logo-slogan {
    width: 101px;
    height: 19px;
  }
  .main-header__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-wrap: nowrap;
    gap: 33px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding: 55px 0;
    padding-bottom: 0;
    max-width: 1220px;
    margin: auto;
  }
  .logo-img {
    height: 59px;
    width: 70px;
  }
  .logo-slogan {
    width: 118px;
    height: 21px;
  }
  .main-header--active {
    position: absolute;
    z-index: 3;
    background-color: transparent;
    color: #000000;
    width: 100%;
    left: calc(50vw - 1220px / 2);
  }
  .main-header__info {
    gap: 14px;
  }
}
.main-nav--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}
.main-nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.main-nav__item {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  font-family: Oswald, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  padding-top: 22px;
  padding-bottom: 24px;
  border-top: 1px solid #e6e6e6;
}
.main-nav__link {
  color: inherit;
}
.main-nav__link--current {
  color: #000000;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    color: #000000;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link:hover {
    color: #00000079;
  }
  .main-nav__link:focus {
    color: #F2F2F2;
  }
  .main-nav__link--current {
    position: relative;
  }
  .main-nav__link--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -8px;
    left: 0;
    height: 2px;
    background: #68b738;
  }
  .main-nav__item {
    border: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    color: #000000;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link--active {
    color: #ffffff;
  }
  .main-nav__link--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -8px;
    left: 0;
    background: #ffffff;
  }
  .main-nav__link--active:hover {
    color: #ffffff57;
  }
  .main-nav__link--active:focus {
    color: #ffffffb4;
  }
  .main-nav__link--current {
    position: relative;
  }
}
.menu-toggle {
  margin: 0;
  width: 24px;
  border: medium none;
  background: transparent;
  background-image: none;
  background-color: transparent;
}
.main-container__wrapper {
  margin: 0 20px;
}
